body{
    margin:0;
    padding:0;
    box-sizing:border-box;
}
.stacked{
    display:grid;
    aspect-ratio:1/1.5;
}

.stacked > *
{
    grid-column: 1/2;
    grid-row:1/2;
}
 
.gridContent{
    align-self:end;
    padding:10px;
    background-color:#fff;
}

.all_gridContent, .button_gridContent{
    align-self:end;
    padding:30px;
}
.button_gridContent{
    justify-self:center;
}

.product_img{
    object-fit:cover;
    object-position:center; 
    cursor:pointer;
}



.popupCart{
    position:absolute;
    right:5px;
    top:60px;
    z-index:1000;
    
}

.shopping:hover{
    cursor:pointer;
}

.product_img:hover{
    height:100%;
    display:"block";
}




.side_box{
    position:relative;
}
.nav-category{

    display:flex;
    justify-content:space-between;
    grid-gap:20px;
    gap:20px
}
.menu{
    display:none;
}
.sidebar{
    display:none;
    width:100%; 
    height:100vh; 
    overflow:auto;
    position:absolute;
    top:0;
    left:0;
    z-index:1000;
    background-color:#ccc;
    color:#333;
    padding:15px;
    display:flex;
    flex-direction:column;
    grid-gap:15px;
    gap:15px;
    opacity:0.9;
    
}
.link_style{
    display:flex;
    align-items:center;
    grid-gap:5px;
    gap:5px;
    
}
.close_icon{
    color:#333;
}
@media only screen and (max-width:767px)
{
    .nav-category{
        display:none;
    }
    .link-name{
        display:none;
    }
    .menu{
        display:inline-block;
    }
    
    .sidebar{
        display:block;
    }
    
}
::-webkit-input-placeholder{
    color:#333
}
::placeholder{
    color:#333
}
